#about {
  /* background-color: antiquewhite; */
  margin-top: 120px;
}
.a {
  padding-top: 5vh;
  height: 100vh;
  display: flex;
  align-items: center;
}
.a-left {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  background-color: red;
  height: 100%;
}
.a-right {
  margin: auto;
  width: 55%;
  text-align: center;
  border-radius: 50px;
  padding: 20px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}
.a-right:hover {
  transform: scale(1.2);
  transition: 0.4s ease-in-out;
  /* transform: .4s ease-in; */
}
.a-card {
  width: 50%;
  height: 70vh;
  border-radius: 30px;
  position: relative;
  overflow: hidden;
}
.a-card.bg {
  position: absolute;
  top: 50px;
  left: 100px;
  background-color: rgb(53, 53, 53);
}

.a-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.a-title {
  font-weight: 800;
  color: #4070f4;
  text-decoration: underline;
}
.a-sub {
  margin: 20px 0px;
  font-weight: 600;
  font-size: 21px;
  font-family: "Fira Sans", sans-serif;
  color: #202124;
}

.a-desc {
  font-weight: 500;
  margin-top: 30px;
  text-align: left;
  /* font-family: "Rubik", sans-serif; */
}
.a-award {
  margin-top: 40px;
  display: flex;
  align-self: center;
  justify-content: center;
}
.a-award-img {
  width: 120px;
  height: 120px;
  border-radius: 20px;
  margin-right: 60px;
}
.a-award-texts {
  width: 70%;
  margin-top: 20px;
}
.a-award-title {
  font-weight: bold;
  color: #555;
  margin-bottom: 10px;
}
.about__skillsTitle {
  color: var(--color-main);
  font-size: 2.4rem;
  margin-bottom: 1.5rem;
}

#cm {
  text-align: center;
}

@media only screen and (max-width: 900px) {
  .about__skillsTitle {
    font-size: 2rem;
  }
}
.about__skillsList {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  gap: 2rem;
}

@media only screen and (max-width: 900px) {
  .about__skillsList {
    gap: 1.5rem;
  }
}

.about__skillsList > li.btn {
  color: var(--color-main);
}
.about__skillsList > li.btn:hover {
  color: var(--color-light);
  cursor: auto;
}

#skill_img {
  width: 9%;
  text-align: center;
  margin-left: 9%;
  font-size: 80%;
  cursor: pointer;
}
#skill_img:hover {
  transition: 0.5s;
  transform: scale(1.1);
}

@media all and (max-width: 399px) and (min-width: 100px) {
  .a-desc {
    font-weight: 500;
    font-size: 12px;
  }
  .abc {
    width: 10%;
  }

  #about {
    margin-top: 50px;
  }
}
