#projects {
    display: grid;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
  }
  
  #buttonnn{
    display: "flex" ;
    justify-content: space-between ;
    margin-Top: "10px";
    margin-left: -50px;
  }
  
  .container {
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 120px;
    flex-wrap: wrap;
    padding: 40px 0px;
  }
  
  .container .card {
    position: relative;
    width: 500px;
    height: 680px;
    box-shadow: inset 5px 5px 5px rgba(0, 0, 0, 0.13),
      inset -5px -5px 5px rgba(255, 255, 255, 0.5),
      5px 5px 5px rgba(0, 0, 0, 0.13), -5px -5px 5px rgba(255, 255, 255, 0.5);
  
    border-radius: 15px;
    margin: 30px;
  }
  
  .container > .card > .box {
    position: absolute;
    top: 20px;
    left: 20px;
    right: 20px;
    bottom: 20px;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.2);
    border-radius: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 0.5s;
    overflow: hidden;
    
  }
  .container .card:hover .box {
    transform: translateY(-20px);
    box-shadow: rgba(0, 0, 0, 0.9) 0px 10px 40px;
    background: linear-gradient(45deg, #dfe6e4, #100d1b);
    transition: 0.9s;
    color:white;
  }
  
  .container .card .box .content {
    padding: 20px;
    text-align: center;
    overflow: hidden;
  }
  .container .card .box .content h2 {
    position: absolute;
    top: -10px;
    right: 20px;
    font-size: 8em;
    color: rgba(0, 0, 0, 0.05);
  }
  
  .container .card:hover .box .content h2 {
    color: rgba(0, 0, 0, 0.05);
    color: white;
  }
  .container .card .box .content h3 {
    font-size: 1.4em;
    color: #4070f4;;
    padding-bottom: 0px;
    margin-top: 0px;
  }
  .container .card:hover .box .content h3 {
    z-index: 1;
    margin-top: 10px;
    transition: 0.5s;
    color:white;
  }
  
  .container .card:hover .box .content h3 p{
    color: white;
  }
  
  .container .card:hover .box .content div p {
    color: rgb(187, 32, 32);
  }
  
  .container .card .box .content div img {
    width: 420px;
    height: 260px;
    margin-top: -30px;
    display: block;
    transition: 0.3s;
    margin-bottom: 10px;
  }
  
  .one {
    position: relative;
    display: inline-block;
    padding: 8px 20px;
    background: #cc03f4;
    margin-top: 15px;
    color: white;
    text-decoration: none;
    font-weight: 400;
    border-radius: 20px;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.02);
    margin-left: 60px;
  }
  .one:last-child {
    margin-left: 40px;
  }
  .container .card:hover .box .content a {
    background-color: #56ffad;
    color: white;
  }
  
  #hoverwhite:hover{
    color:white;
  }
  
